@import 'reset';
@import 'mixin';
@import 'grid';

body {
    font-size: 16px;
    font-family: "Roboto","Microsoft JhengHei","PingFang TC";
    .container-fluid {
        height: 100vh;
        width: 100vw;
        overflow-x: hidden;
        padding: 0px;
        .navBar {
            background-color: rgba(0,0,0,0.8);
            position: fixed;
            width: 100%;
            z-index: 10;
            box-shadow: 0 0 10px 0 rgba(0,0,0,0.5);
            .menu {
                li {
                    a {
                        display: block;
                        padding: 10px 15px;
                        text-decoration: none;
                        letter-spacing: 1.5px;
                        color: #a7a7a7;
                        &:hover {
                            color: #fff; } }
                    .active {
                        color: #fff; } } }
            .contactIcon {
                a {
                    color: #a7a7a7;
                    text-decoration: none;
                    i {
                        font-size: 20px;
                        &:hover {
                            color: #fff; } } } } }
        .Portfolio {
            width: 80%;
            margin: 0 auto;
            margin-top: 100px;
            .row {
                margin: 30px 0; }
            .col {
                height: 100px;
                background-color: red;
                margin: 0 1%;
                height: auto;
                max-height: 360px;
                overflow: hidden;
                border-radius: 10px;
                background-image: linear-gradient(45deg, #3f3f46 0%,#6A5ACD 100%);
                .effectItem {
                    max-width: none;
                    width: calc(100% + 100px);
                    transition: opacity 0.35s, transform 0.35s;
                    transform: translate3d(-50px,0,0); }
                img {
                    position: relative;
                    display: block;
                    min-height: 100%;
                    max-width: 100%;
                    opacity: 0.8; }
                .Item {
                    padding: 1.5em;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    text-decoration: none;
                    color: white;
                    &::before {
                        position: absolute;
                        top: 20px;
                        right: 20px;
                        bottom: 20px;
                        left: 20px;
                        border: 1px solid #fff;
                        content: '';
                        opacity: 0;
                        transition: opacity 0.35s, transform 0.35s;
                        transform: translate3d(-20px,0,0); }
                    h2 {
                        transition: all .3s;
                        transform: translate3d(20px,0,0);
                        margin-bottom: 10px;
                        margin-left: 20px;
                        font-size: 32px;
                        letter-spacing: 1.5;
                        opacity: 0; }

                    p {
                        transform: translate3d(-20px,0,0);
                        transition: all .3s;
                        margin-left: 20px;
                        letter-spacing: 1.2;
                        opacity: 0; } } }
            .active {
                .effectItem {
                    opacity: 0.4;
                    transform: translate3d(-20px,0,0); }
                .Item {
                    &:before {
                        opacity: 1;
                        transform: translate3d(0px,0,0); }
                    h2 {
                        opacity: 1;
                        transform: translate3d(0px,0,0); }
                    p {
                        opacity: 1;
                        transform: translate3d(0px,0,0); } } } } } }
